<template>
  <div id="deck">
    <a-page-header style="border: 1px solid rgb(235, 237, 240)" title="re0webxoss" @back="returnIndex"></a-page-header>
    <a-tabs type="editable-card" @edit="onEdit">
      <a-tab-pane v-for="deck in this.$store.state.deckEditor_deckList" :key="deck.key" :tab="deck.title">
        <!-- 这里的src也是动态的，需要定义到中， -->
        <img v-for="(card,key) in deck.mainDeck"  @click="cardInfo_(card)" :src="cardInfo_(card)" :alt="card" :key="key">
        <!-- <div id="mainDeck" class="cardDeck">
          渲染出deckList里的mainDeck里的牌到v-for img中。
          <img :v-for="card in deck" :src="require('../assets/images/' + item + '.jpg')" alt="">
        </div>
        <div id="lrigDeck" class="cardDeck"></div> -->
      </a-tab-pane>
    </a-tabs>
    <div id="mainDeck" class="cardDeck">
      <!-- <img v-for="(item,index) in this.$store.state.myDeck" :key="index" :src="require('../assets/images/' + item + '.jpg')" @click="removeCard(index)" @mouseover="showImg(item)"> -->
    </div> 
    <div id="lrigDeck" class="cardDeck"></div>
  </div>
</template>

<script>
  export default {
    data() {
      return {

      }
    },
    methods: {
      returnIndex() {
        this.$router.replace('/index')
      },
      removeCard(index) {
        this.$store.commit('removeCard', index)
      },
      showImg(item) {
        this.$store.commit('showImg', item)
      },
      callback(key) {
        console.log(key);
      },
      onEdit(targetKey, action) {
        this[action](targetKey);
      },
      // onChange(activeKey) {
      //   this.$store.state.activeKey = activeKey
      //   // this.$store.state.myDeck = this.$store.state.decks[activeKey-1].mainDeck
      // },
      //添加卡组，操作deckList
      //这里不推荐直接修改，而是用commit
      add() {
        this.$store.commit('addDeck')
        // localStorage.setItem('newDeck', '')
      },
      //移除卡组
      remove(targetKey) {
        this.$store.commit('removeDeck',targetKey)
        // localStorage.removeItem(targetKey)
      },
      cardInfo_(card) {
        //需要使用正则表达式，使得可以通过card的id找到对应的扩展包。 WXDi01_D01_001  取第二个_前的为pack
        let card_ =  card.split('_')
        console.log(card_)
      }
      // initDeck() {
      //   this.$store.commit('initMyCard')
      // }


      // deleteCard() {
      //   this.$store.commit('deleteCard')
      // }
    },
    created() {
      // this.deck = Object.keys(localStorage).filter(deck => {return deck.indexOf('deck_')})
      let mydeck = localStorage.getItem('myDeck') || ''
      this.$store.state.myDeck = mydeck.split(',')
    }
  }
</script>

<style>
  #deck {
    height: 100vh;
  }

  .card-list {
    height: 100%;
    background-color: pink;
  }

  #mainDeck {
    width: 100%;
    height: 0;
    padding-bottom: 70%;
    border: 1px solid red;
    margin-bottom: 50px;
  }

  #lrigDeck {
    width: 100%;
    height: 200px;
    border: 1px solid red;
  }

  .cardDeck>img {
    width: 10%;
  }
</style>